import moment from 'moment'
import 'moment/locale/zh-cn'
moment.locale('zh-cn')
export default {
	data() {
		return {
			postData: [{ created: 0 }],
			isLoading: true
		}
	},
	filters: {
		formatTime(value) {
			return moment.unix(value).format('YYYY年MM月DD日')
		}
	},
	async onLoad(query) {
		this.postData = await this.$api.getPostBycid({ cid: query.cid })
		console.log('请求成功')
		this.isLoading = false
	}
}
